@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    margin: 0 auto;
    height: 100%;
}

.header {
    min-height: 60px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    background: #F9F9FE;
}

.main {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: linear-gradient(180deg,  #8CE0EB 0%, #D2CCB1 100%);
    height: calc(100vh - 60px);
}

.mainBlock{
    display: flex;
    background-color: white;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0px 4px 18px rgba(115, 146, 108, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
}

.left__panel {
    min-width: 240px;
    padding: 25px;
}

.bottom__panel {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    padding-top: 15px;
}


.authBlock button {
    margin: 40px 0;
    border-radius: 40px;
    box-shadow: 0px 4px 18px rgba(33, 38, 143, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
}

.button__primary {
    background-color: chartreuse;
}


h2 {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 33px;
    text-align: right;
    color: #2D2E46;
    word-wrap: break-word;
}

h4 {
    font-family: Poppins;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: #2D2E46;
    opacity: 0.5;
}



.Paginator_selectedPage__1u1Cm {
    font-weight: bold;
}

.Paginator_paginator__1VFdz {
    margin: 10px;
    display: inline-block;
}

.Paginator_pageNumber__MoS0k {
    position: relative;
    display: inline-block;
    width: 2em;
    height: 2em;
    line-height: 2em;
    vertical-align: middle;
    text-align: center;
    text-decoration: none;
    color: #000;
    outline: none;
    border-radius: 5px;
    box-shadow: 0 0 0 1px #ddd inset, 0 1px 1px #fff;
}
.Paginator_pageNumber__MoS0k:hover {
    background: #dcdcdc linear-gradient(#fff, #dcdcdc);
    box-shadow: 0 0 0 1px #aaa inset, 0 1px 1px #aaa;
}
.Paginator_pageNumber__MoS0k:active {
    background: none;
    box-shadow: 0 0 0 1px #bbb inset, 0 1px 3px rgba(0,0,0,.5) inset, 0 1px 2px #fff;
}

.Paginator_pageNumber__MoS0k.Paginator_selectedPage__1u1Cm {
    font-weight: bold;
    border-color: black;
}
.Paginator_arrowLeft__bvzOo {
    position: relative;
    width: 25px;
    height: 25px;
    border: 3px solid gray;
    border-radius: 50%;
}
 :disabled {
    opacity:0.1;
}
.Paginator_arrowLeft__bvzOo:after {
    position: absolute;
    top: 5px;
    left: 6px;
    content: '';
    display: block;
    width: 7px;
    height: 7px;
    border-left: 3px solid gray;
    border-bottom: 3px solid gray;
    transform: rotate(45deg);
}

.Paginator_arrowRight__2Jh-l {
    position: relative;
    width: 25px;
    height: 25px;
    border: 3px solid gray;
    border-radius: 50%;
}
.Paginator_arrowRight__2Jh-l:after {
    position: absolute;
    top: 5px;
    left: 3px;
    content: '';
    display: block;
    width: 7px;
    height: 7px;
    border-left: 3px solid gray;
    border-bottom: 3px solid gray;
    transform: rotate(225deg);
}

